import React, { Component } from 'react';
import { AsyncTypeahead, Menu, MenuItem, Highlighter } from 'react-bootstrap-typeahead';
import Types from 'prop-types';

import 'react-bootstrap-typeahead/css/Typeahead.css';

import Chip from './chip/Chip';
import './Typeahead.less';

const SIZE_MAPPING = {
  sm: 'small',
  md: undefined,
  lg: 'large',
};

export default class Typeahead extends Component {
  static propTypes = {
    id: Types.string.isRequired,
    options: Types.arrayOf(Types.object).isRequired,
    allowNew: Types.bool,
    autoFocus: Types.bool,
    clearable: Types.bool,
    iconDivider: Types.bool,
    isLoading: Types.bool,
    multiple: Types.bool,
    showSuggestions: Types.bool,
    icon: Types.string,
    maxHeight: Types.string,
    placeholder: Types.string,
    footer: Types.node,
    filterBy: Types.func,
    validateChip: Types.func,
    onSearch: Types.func,
    onChange: Types.func,
    chipSeparators: Types.arrayOf(Types.string),
    size: Types.oneOf(['sm', 'md', 'lg']),
  };

  static defaultProps = {
    allowNew: false,
    autoFocus: false,
    clearable: true,
    iconDivider: false,
    isLoading: false,
    multiple: false,
    showSuggestions: true,
    icon: null,
    maxHeight: 'none',
    placeholder: null,
    footer: null,
    onSearch: () => {},
    onChange: undefined,
    validateChip: () => true,
    filterBy: undefined,
    size: 'md',
    chipSeparators: [],
  };

  constructor() {
    super();
    this.renderMenu = this.renderMenu.bind(this);
    this.clear = this.clear.bind(this);
    this.renderToken = this.renderToken.bind(this);
    this.onPaste = this.onPaste.bind(this);
    this.onChange = this.onChange.bind(this);
    this.onInputChange = this.onInputChange.bind(this);
  }

  state = {
    selected: null,
    errorState: false,
    query: '',
  };

  onChange = selected => {
    this.updateSelectedValue(selected);
  };

  onInputChange = query => {
    this.setState({ query });
  };

  onPaste = event => {
    const { allowNew, multiple, chipSeparators } = this.props;
    if (allowNew && multiple && chipSeparators.length > 0) {
      const value = event.clipboardData.getData('text');
      const regex = new RegExp(chipSeparators.join('|'));
      if (value) {
        const pastedChips = value.split(regex).map(label => ({ label: label.trim() }));
        const selected = []
          .concat(this.state.selected, pastedChips)
          .filter(chip => chip && chip.label);

        this.updateSelectedValue(selected);
        event.preventDefault();
      }
    }
  };

  onKeyDown = event => {
    const { showSuggestions, allowNew, multiple, chipSeparators } = this.props;

    if (!showSuggestions && allowNew && multiple) {
      if (['Enter', ...chipSeparators].includes(event.key)) {
        let selected = [];
        if (this.state.selected) {
          selected = [...this.state.selected];
        }
        const { value } = this.typeahead.getInstance().getInput();
        if (value) {
          selected.push({ label: this.typeahead.getInstance().getInput().value });
          this.typeahead.getInstance().clear();
          this.updateSelectedValue(selected);
        }

        event.preventDefault();
      }
    }

    if (this.props.multiple && event.key === 'Backspace') {
      const { value } = this.typeahead.getInstance().getInput();
      if (!value && this.state.selected) {
        this.updateSelectedValue(this.state.selected.slice(0, -1));
      }
    }

    return false;
  };

  updateSelectedValue = selected => {
    const { onChange, validateChip } = this.props;
    const errorState = selected.some(chip => !validateChip(chip));

    this.setState({ selected, errorState });

    if (onChange) {
      onChange(selected);
    }
  };

  clear = event => {
    event.preventDefault();
    this.typeahead.getInstance().clear();
    this.updateSelectedValue([]);
  };

  renderToken = (option, tokenProps, idx) => {
    const { disabled, tabIndex, onRemove } = tokenProps;
    const valid = this.props.validateChip(option);
    const { size } = this.props;

    return (
      <Chip
        disabled={disabled}
        tabIndex={tabIndex}
        key={idx}
        valid={valid}
        size={size}
        option={option}
        onRemove={onRemove}
      />
    );
  };

  renderItem = (item, menuProps, idx) => {
    const { text } = menuProps;
    const { label, note, secondary } = item;

    return (
      <MenuItem position={idx} option={item} key={idx} id={idx}>
        <Highlighter search={text.trim()}>{label}</Highlighter>
        {note ? <span className="small m-l-1">{note}</span> : ''}
        {secondary ? <span className="small text-ellipsis">{secondary}</span> : ''}
      </MenuItem>
    );
  };

  renderMenu = (results, menuProps) => {
    const { showSuggestions, footer } = this.props;
    if (showSuggestions) {
      return (
        <Menu {...menuProps} className="btn-group">
          {results.map((result, idx) => this.renderItem(result, menuProps, idx))}
          {footer ? <div className="options-footer">{footer}</div> : ''}
        </Menu>
      );
    }
    return null;
  };

  render() {
    const {
      id,
      onSearch,
      multiple,
      placeholder,
      size,
      icon,
      iconDivider,
      clearable,
      allowNew,
      autoFocus,
      filterBy,
      isLoading,
      maxHeight,
      options,
    } = this.props;

    const { errorState, query, selected } = this.state;
    const typeaheadSize = SIZE_MAPPING[size];
    const clearButton = clearable && (query || (selected && selected.length > 0));

    return (
      <div
        className={`typeahead ${!iconDivider ? 'typeahead-no-divider' : ''} ${
          selected && selected.length > 0 ? 'typeahead-has-value' : 'typehead-empty'
        }`}
      >
        <div className={`form-group ${errorState ? 'has-error' : ''}`}>
          <div className={`input-group input-group-${size}`}>
            {icon && (
              <span className="input-group-addon input-group-prepend">
                <i className={` input-group-text icon icon-${icon}`} />
              </span>
            )}

            <AsyncTypeahead
              id={id}
              onChange={this.onChange}
              selected={selected}
              renderMenuItemChildren={this.renderItem}
              useCache={false}
              renderMenu={this.renderMenu}
              renderToken={this.renderToken}
              onKeyDown={this.onKeyDown}
              ref={typeahead => {
                this.typeahead = typeahead;
              }}
              inputProps={{
                maxLength: 70,
                value: '',
                onPaste: this.onPaste,
              }}
              onInputChange={this.onInputChange}
              bsSize={typeaheadSize}
              allowNew={allowNew}
              isLoading={isLoading}
              filterBy={filterBy}
              onSearch={onSearch}
              options={options}
              maxHeight={maxHeight}
              multiple={multiple}
              placeholder={placeholder}
              autoFocus={autoFocus}
              selectHintOnEnter
            />

            <span className="input-group-addon input-group-append">
              {clearButton && (
                <button className="btn-unstyled" onClick={this.clear}>
                  <i className=" input-group-text icon icon-close" />
                </button>
              )}
            </span>
          </div>
        </div>
      </div>
    );
  }
}



