import React, { Component } from 'react';
import { Typeahead } from '..';
import { generateCodeBlock, generateState, generateInput } from '../../docs/utils';

const SIZES = ['sm', 'md', 'lg'];
const options = [
  {
    label: 'A thing',
    note: 'with a note',
  },
  {
    label: 'Another thing',
    secondary: 'with secondary text this time',
  },
  {
    label: 'Profile',
    icon: 'icon-profile',
  },
  {
    label: 'Globe',
    icon: 'icon-globe',
  },
  {
    label: 'British Pound',
    currency: 'gbp',
  },
  {
    label: 'Euro',
    currency: 'eur',
  },
  {
    label: 'Something else',
  },
];

const KNOBS = {
  knobs: [
    {
      type: 'select',
      label: 'Size',
      state: 'size',
      options: SIZES.map(value => ({
        value,
        label: value,
      })),
      defaultState: { value: SIZES[1], label: SIZES[1] },
    },
    {
      type: 'checkbox',
      label: 'Multiselect (chips mode)',
      state: 'multiple',
      defaultState: false,
    },
    {
      type: 'checkbox',
      label: 'Clearable',
      state: 'clearable',
      defaultState: false,
    },
    {
      type: 'text',
      label: 'Icon name',
      state: 'icon',
      defaultState: undefined,
    },
    {
      type: 'checkbox',
      label: 'Icon Divider',
      state: 'iconDivider',
      defaultState: undefined,
    },
    {
      type: 'checkbox',
      label: 'Allow new',
      state: 'allowNew',
      defaultState: false,
    },
    {
      type: 'checkbox',
      label: 'Show suggestions',
      state: 'showSuggestions',
      defaultState: true,
    },
    {
      type: 'text',
      label: 'Placeholder',
      state: 'placeholder',
      defaultState: 'Enter your search request',
    },
  ],
};

export default class TypeaheadDocs extends Component {
  state = {
    options,
    isLoading: false,
    ...generateState(KNOBS),
  };
  onSuggestionsFetchRequested = () => {
    this.setState({ options });
  };

  render() {
    const {
      size,
      multiple,
      icon,
      clearable,
      iconDivider,
      allowNew,
      showSuggestions,
      placeholder,
    } = this.state;

    const footer = (
      <div>
        Want a footer? <a href="/manual">Style it!</a>
      </div>
    );

    const validateChip =
      multiple && allowNew
        ? option =>
            /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
              option.label,
            )
        : undefined;

    return (
      <div className="container">
        <section className="section">
          <div className="row">
            <div className="col-md-6">
              <h2>Typeahead</h2>
              <p>
                My <i>type</i>ahead on paper
              </p>
              <p>
                Also chips. For chips turn off showSuggestions and turn on multiple and allowNew!
              </p>
              {generateCodeBlock('Typeahead', KNOBS, this, {
                options: JSON.stringify(options, null, '  '),
                validateChip,
              })}
              <p>
                Required:
                <ul>
                  <li>id</li>
                  <li>options</li>
                </ul>
              </p>
            </div>
            <div className="col-md-6">
              <Typeahead
                id="typeahead"
                options={this.state.options}
                isLoading={this.state.isLoading}
                size={size.value}
                filterBy={() => true}
                footer={footer}
                multiple={multiple}
                icon={icon}
                iconDivider={iconDivider}
                clearable={clearable}
                allowNew={allowNew}
                showSuggestions={showSuggestions}
                placeholder={placeholder}
                chipSeparators={[',', ' ']}
                validateChip={validateChip}
                onSearch={() => {
                  this.setState({ isLoading: true });
                  setTimeout(() => this.setState({ isLoading: false, options }), 1500);
                }}
              />
              <div className="row m-t-5">{KNOBS.knobs.map(knob => generateInput(knob, this))}</div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}



