import React from 'react';
import Types from 'prop-types';
import { tokenContainer } from 'react-bootstrap-typeahead';
import Close from '@transferwise/icons/react/close';

import './Chip.less';

const Chip = props => {
  const { className, valid, size, option, onRemove } = props;
  return (
    <div
      className={`chip rbt-token rbt-token-removeable chip-${size} ${
        valid ? '' : 'has-error'
      } ${className}`}
    >
      {option.label}
      <button className="btn-unstyled remove-chip" onClick={onRemove}>
        <Close size="sm" />
      </button>
    </div>
  );
};

Chip.propTypes = {
  option: Types.objectOf(Types.string).isRequired,
  onRemove: Types.func.isRequired,
  className: Types.string,
  size: Types.string,
  valid: Types.bool,
};

Chip.defaultProps = {
  className: '',
  size: 'md',
  valid: true,
};
export default tokenContainer(Chip);

export const Undecorated = Chip;



